<template>
    <tr>
        <!-- td v-for="i in full" :key="i">&nbsp;</td -->
        <td :colspan="long">
            &nbsp;
        </td>
    </tr>
</template>

<script>
    export default {
        props: {
            long: {
                type: Number,
                default: 7
            },
            equal: {
                type: Boolean,
                default: true
            }
        },
        name: '',
        computed: {
            full() {
                return new Array(this.long)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


