<template>
    <pdf-template>
        <div class="fs-XXL fw-b text-center" slot="header">
            Essence Global CO. Limited
        </div>

        <pdf-black-line-table slot="content">
            <pdf-user-tbody slot="body" :_me="mine" :_childs="childrens"></pdf-user-tbody>
        </pdf-black-line-table>

        <pdf-user-footer slot="footer" :me="mine"></pdf-user-footer>
    </pdf-template>
</template>

<script>
import api_bonus from '../../../common/es/api_bonus/index'

import PdfBlackLineTable from '../../../components/Pdf/PdfBlackLineTable.vue'
import PdfTemplate from '../../../components/Pdf/PdfTemplate.vue'
import UiPageHeader from '../../../components/Ui/UiPageHeader.vue'
import PdfUserFooter from './Inner/PdfUserFooter.vue'
import PdfUserTbody from './PdfUserTbody.vue'

    export default {
  components: { PdfTemplate, UiPageHeader, PdfBlackLineTable, PdfUserFooter, PdfUserTbody },
        name: '',
        data() {
            return {
                mine: null,
                childrens: null
            }
        },
        mounted() {
            this.lockMine()
        },
        methods: {
            lockMe(user, transfer) {
                if (user.unid == this.$store.state.user.unid) {

                    let res = JSON.parse(JSON.stringify(user))
                    this.mine = res
                }
            },
            lockChilds(user, transfer) {
                if (user.unid != this.mine.unid) {
                    this.childrens.push(user)
                }
            },
            async lockMine() {
                let many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer
                
                if (many) {
                    api_bonus._build_res(many, transfer, this.lockMe)
                }

                const usd_tl = await api_bonus.team_layer( [ this.mine ], transfer)
                const usd_re = await api_bonus.recommend( [ this.mine ], transfer )

                if (this.mine) {
                    this.childrens = [ ]
                    api_bonus._build_res([ this.mine ], transfer, this.lockChilds)
                }
            },
            back() {
                this.$router.push({
                    path: '/home/account' })
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


