<template>
    <tbody class="fs-L">

        <!-- Bonus Statement -->
        <tr-one-line><div>Bonus Statement</div></tr-one-line>
        <tr-empty></tr-empty>

        <tr>
            <td class="t-r">Member ID:&nbsp;</td>
            <td>{{ $store.state.user.number_code }}</td>
            <td></td><td></td><td></td>
            <td class="t-r" colspan="2">
                {{ view.getTodayEn() }}
            </td>
        </tr>
        <tr>
            <td class="t-r">Member Name:&nbsp;</td>
            <td>{{ $store.state.user.username }}</td>
            <td></td><td></td>
            <td class="t-r" colspan="3">
                Contents Period: 由&nbsp;{{ view.ser_timed($store.state.bonus.chronu.start, true) }}至&nbsp;{{ view.ser_timed($store.state.bonus.chronu.end, true) }}
            </td>
        </tr>
        <tr-empty></tr-empty>

        <!-- MSG -->
        <tr class="fw-b">
            <td class="t-c fs-XL">Bonus Eligibility:</td>
            <td class="t-c fs-XL">active</td>
            <td colspan="5"></td>
        </tr>
        <pum-buys :is_header="true"></pum-buys>
                <tr v-for="(v, i) in buys_me" :key="i">
                    <td>
                        {{ view.ser_timed(v.buy_time, true) }}
                    </td>
                    <td>
                        {{ $store.state.user.number_code }}
                    </td>
                    <td>
                        Member
                    </td>
                    <td>
                        {{ v.order_code }}
                    </td>
                    <td>
                        {{ v.num * v.product.pv }}
                    </td>
                    <td colspan="2"></td>
                </tr>
        <tr>
            <td colspan="5" class="t-r">Total Credit:&nbsp;{{ buys_me_res }}</td>
            <td colspan="2"></td>
        </tr>

        <tr class="fw-b">
            <td class="t-c fs-XL">本周期支付级别</td>
            <td class="t-c fs-XL">Member Level 4</td>
            <td colspan="5"></td>
        </tr>
        <pum-credit :is_header="true"></pum-credit>
        <pum-credit :me="_me"></pum-credit>
        <tr-empty></tr-empty>
        
        <!-- Enroller referral -->
        <tr-one-line><div class="fs-XXL fw-b">Enroller referral</div></tr-one-line>
        <tr-empty-small></tr-empty-small>

        <pdf-user-bonus-recommend :is_header="true"></pdf-user-bonus-recommend>
        
                    <tr v-for="(v, i) in buying_re" :key="i" class="text-center">
                        <td>
                            {{ v.users_permissions_user.number_code }}
                        </td>
                        <td class="text-left pl-2">
                            {{ v.users_permissions_user.display_name }}
                        </td>
                        <td>
                            {{ v.user_sponser.index - _me.index }}
                        </td>
                        <td>{{ v.order_code }}</td>
                        <td>
                            {{ v.product.pv * v.num }} 
                        </td>
                        <td>
                            {{ view.floatMul(re_ratios[ v.user_sponser.index - _me.index - 1 ],  100) }}%
                        </td>
                        <td>
                            {{ view.floatMul(re_ratios[ v.user_sponser.index - _me.index - 1 ],  v.user_sponser.ipv) }}
                        </td>
                    </tr>

        <tr class="t-r">
            <td colspan="6">
                Total Enroller referral bonus for first purchase credit:
            </td>
            <td>
                354
            </td>
        </tr>
        <tr class="t-r">
            <td colspan="6">
                Total Bonus:
            </td>
            <td class="t-l">
                HK$&nbsp;&nbsp;&nbsp;&nbsp;{{ _me.bonus.usd_recommend }}
            </td>
        </tr>
        <tr-empty></tr-empty>

        <!-- Active Branch Bonus -->
        <tr-one-line><div class="fs-XXL fw-b">Active Branch Bonus</div></tr-one-line>
        <tr-empty-small></tr-empty-small>

        <pdf-user-bonus-active-house :is_header="true"></pdf-user-bonus-active-house>
        
                    <tr v-for="(v, i) in buying_ah" :key="i" class="text-center">
                        <td>
                            {{ v.users_permissions_user.number_code }}
                        </td>
                        <td class="text-left pl-2">
                            {{ v.users_permissions_user.display_name }}
                        </td>
                        <td>
                            {{ v.user_sponser.index - _me.index }}
                        </td>
                        <td>
                            {{ v.order_code }}
                        </td>
                        <td>
                            {{ v.product.pv * v.num }}
                        </td>
                    </tr>

        <tr class="t-r">
            <td colspan="6"> Total Active Branch credit: </td> 
            <td> 1 </td>
        </tr>
        <tr class="t-r">
            <td colspan="6"> Total Bonus: </td>
            <td class="t-l"> US$&nbsp;{{ _me.bonus.usd_active_house }} </td>
        </tr>
        <tr-empty></tr-empty>

        <!-- Organization Bonus -->
        <tr-one-line><div class="fs-XXL fw-b">Organization Bonus</div></tr-one-line>
        <tr-empty-small></tr-empty-small>

        <pdf-user-bonus-team-layer :is_header="true"></pdf-user-bonus-team-layer>
        
                    <tr v-for="(v, i) in buying_tl" :key="i" class="text-center">
                        <td>
                            {{ v.users_permissions_user.number_code }}
                        </td>
                        <td class="text-left pl-2">
                            {{ v.users_permissions_user.display_name }}
                        </td>
                        <td>
                            {{ v.user_sponser.temporary_index }}
                        </td>
                        <td>{{ v.order_code }}</td>
                        <td>
                            {{ v.product.pv * v.num }}
                        </td>
                        <td>
                            {{ view.floatMul(_me.vip.ratio[v.user_sponser.temporary_index - 1],  100) }}%
                        </td>
                        <td>
                            {{ view.floatMul(_me.vip.ratio[v.user_sponser.temporary_index - 1],  v.user_sponser.ipv) }}
                        </td>
                    </tr>

        <tr class="t-r">
            <td colspan="6"> Total Organization credit: </td> 
            <td> 1 </td>
        </tr>
        <tr class="t-r">
            <td colspan="6"> Total Bonus: </td>
            <td class="t-l"> HK$&nbsp;&nbsp;&nbsp;&nbsp;{{ _me.bonus.usd_team_layer }} </td>
        </tr>
        <tr-empty></tr-empty>

    </tbody>
</template>

<script>
import config from '../../../common/es/api_bonus/conf'
import api_bonus from '../../../common/es/api_bonus/index'

import TrEmpty from '../../../components/Pdf/TrEmpty.vue'
import TrEmptySmall from '../../../components/Pdf/TrEmptySmall.vue'
import TrOneLine from '../../../components/Pdf/TrOneLine.vue'
import PumBuys from './Msg/PumBuys.vue'
import PumCredit from './Msg/PumCredit.vue'
import PdfUserBonusActiveHouse from './Tr/PdfUserBonusActiveHouse.vue'
import PdfUserBonusRecommend from './Tr/PdfUserBonusRecommend.vue'
import PdfUserBonusTeamLayer from './Tr/PdfUserBonusTeamLayer.vue'

    export default {
  components: { TrOneLine, TrEmpty, PdfUserBonusRecommend, TrEmptySmall, PumBuys, PumCredit, PdfUserBonusActiveHouse, PdfUserBonusTeamLayer },
        name: '',
        props: {
            _me: {
                type: Object
            },
            _childs: {
                type: Array
            }
        },
        data() {
            return {
                buys_me: [ ],

                re_ratios: [ ],

                buying_re: [ ],
                buying_ah: [ ],
                buying_tl: [ ],
            }
        },
        mounted() {
            this.re_ratios = config.RECOMMEND_RATIOS
            this.buys_me_Loading()

            this.buying_re = this.buying_Loading('re')
            this.buying_ah = this.buying_Loading('ah')
            this.buying_tl = this.buying_Loading('tl')
        },
        computed: {
            buys_me_res() {
                let res = 0
                this.buys_me.map(e => {
                    res += (e.num * e.product.pv)
                })
                return res
            }
        },
        methods: {

            async buying_Loading(code = 're') {
                let listing = [ ]

                let resource = this.childs.filter(e => {
                    if (code == 're') {

                        if (e.ipv_first > 0) { return true }
                    }
                    else if (code == 'ah') {

                        if (e.index - this._me.index <= 2) { return true }
                    } else if (code == 'tl') {

                    }
                    
                    return false
                })
                resource.map(async e => {
                    
                    let res = await this.conn.get(
                        this.api.buys,
                        this.$store.state.token,
                        {
                            'chronu': this.$store.state.chronus.id,
                            'users_permissions_user': e.id,
                            '_sort': 'published_at:DESC'
                        }
                    )

                    if (res) {
                        res.map(j => {
                            j.user_sponser = e
                            listing.push(j)
                        })
                    }
                    
                })

                return listing
            },
            async buys_me_Loading() {
                const buys = await this.conn.get(
                    this.api.buys,
                    this.$store.state.token,
                    {
                        'users_permissions_user': this.$store.state.user.id
                    }
                )

                if (buys) {
                    this.buys_me = buys

                }
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


